@font-face {
  font-family: Noto Sans;
  src: url("NotoSans-Regular.aad1ada3.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: Noto Sans;
  src: url("NotoSans-Medium.a84568cd.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Noto Sans;
  src: url("NotoSans-SemiBold.5147a5e6.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: Roboto Mono;
  src: url("RobotoMono-Regular.46f0c783.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --base-font-size: 20px;
  --block-border-radius: 12px;
  --button-border-radius: 8px;
  --color-grey: #ebebeb;
  --color-black: #000;
  --color-light-black: #414141;
  --color-white: #fff;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Noto Sans, sans-serif;
  font-weight: 400;
  line-height: 1.25;
  font-size: calc(var(--base-font-size) * .9);
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 120px;
}

.main {
  margin: 80px 0 250px;
}

a {
  color: var(--color-black);
  text-decoration: none;
  position: relative;
}

a:not(.select-none):after {
  content: "";
  border-radius: var(--button-border-radius);
  background-color: #0000;
  width: 5px;
  height: 5px;
  transition: background-color .125s ease-out;
  display: block;
  position: absolute;
  top: calc(50% - 1px);
  left: calc(100% + 5px);
}

@media (width >= 640px) {
  a:not(.select-none):hover:after {
    background-color: var(--color-black);
  }
}

h1 {
  font-size: calc(var(--base-font-size) * 1.15);
  font-weight: 600;
}

h2 {
  font-size: calc(var(--base-font-size) * 1.1);
  padding-top: 16px;
  font-weight: 500;
}

h3 {
  font-size: calc(var(--base-font-size) * 1);
  padding-top: 12px;
}

h1, h2, h3 {
  text-align: center;
}

button {
  cursor: pointer;
  background: none;
  border: none;
}

.button {
  font-size: calc(var(--base-font-size) * .8);
  border-radius: var(--button-border-radius);
  color: var(--color-white);
  background-color: var(--color-black);
  padding: 10px 25px;
  transition: background-color .1s linear;
}

@media (width >= 640px) {
  .button:hover {
    background-color: var(--color-light-black);
  }
}

svg {
  transition: fill .25s ease-in-out;
}

.select-none {
  user-select: none;
}

@media (width <= 1024px) {
  body {
    max-width: 100%;
    padding: 0 20px;
  }

  .main {
    margin: 20px 0 150px;
  }

  h1 {
    font-size: calc(var(--base-font-size) * 1.1);
  }

  h2 {
    font-size: var(--base-font-size);
  }
}

.header {
  justify-content: end;
  padding-top: 40px;
  display: flex;
}

.header__menu-button {
  transition: transform .1s linear;
}

.header__menu-button_clicked {
  transform: rotate(90deg);
}

.header__menu-button svg {
  stroke: var(--color-black);
}

.header__menu-icon {
  transition: fill .125s ease-out;
}

.header__menu {
  border-radius: var(--block-border-radius);
  background-color: var(--color-grey);
  opacity: 1;
  visibility: visible;
  padding: 20px 30px;
  transition: opacity .1s linear, visibility .1s linear;
  display: block;
  position: absolute;
  top: 20px;
  right: 180px;
}

.header__menu_closed {
  opacity: 0;
  visibility: hidden;
}

.menu-list {
  grid-template: 1fr / repeat(3, auto);
  gap: 12px 36px;
  display: grid;
}

.menu-list__item:first-child {
  order: 1;
}

.menu-list__item:nth-child(2) {
  order: 4;
}

.menu-list__item:nth-child(3) {
  order: 2;
}

.menu-list__item:nth-child(4) {
  order: 5;
}

.menu-list__item:nth-child(5) {
  order: 3;
}

@media (width <= 1024px) {
  .header {
    padding-top: 20px;
    position: relative;
  }

  .header__menu {
    left: 0;
    right: 60px;
  }

  .menu-list {
    word-break: break-word;
    grid-template: auto / 1fr;
  }
}

.intro {
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  row-gap: 50px;
  display: grid;
}

@keyframes float {
  0% {
    transform: translate(0);
  }

  50% {
    transform: translateY(8px);
  }

  100% {
    transform: translateY(0);
  }
}

.intro__picture {
  animation: 4s infinite float;
}

.intro__image {
  width: 100%;
  max-width: 380px;
}

.intro__content {
  padding: 40px 0 0 120px;
}

.intro__header {
  margin-bottom: 36px;
}

.intro__title {
  margin-bottom: 10px;
}

.intro__subtitle {
  font-weight: 500;
}

.intro__title, .intro__subtitle {
  text-align: start;
}

.intro__paragraph {
  margin-bottom: 15px;
}

.intro__paragraph:last-of-type {
  margin-bottom: 40px;
}

.intro__more-link {
  float: right;
  text-decoration: none;
}

.intro__arrow-down {
  cursor: pointer;
  grid-area: 2 / 1 / 3 / 3;
  transition: transform .2s ease-in;
}

@media (width >= 640px) {
  .intro__arrow-down:hover {
    transition: transform .2s ease-in-out;
    transform: translateY(4px);
  }
}

@media (width <= 1024px) {
  .intro {
    grid-template-columns: 1fr;
    place-items: center;
    gap: 60px 0;
  }

  .intro__picture {
    grid-row: 1 / 2;
    animation: none;
  }

  .intro__image {
    max-width: 250px;
  }

  .intro__content {
    grid-row: 2 / 3;
    padding: 0;
  }

  .intro__arrow-down {
    grid-row: 3 / 4;
  }
}

.forecast {
  max-width: 900px;
  margin: 300px auto 150px;
}

.forecast__title {
  margin-bottom: 24px;
}

.forecast__days-wrapper, .forecast__month-wrapper {
  width: 100%;
}

.forecast__chart {
  margin: 70px 0;
}

.forecast__chart-alternative {
  text-align: center;
  background-color: var(--color-grey);
  border-radius: var(--block-border-radius);
  padding: 30px;
}

@media (width <= 1024px) {
  .forecast {
    margin: 100px 0;
  }

  .forecast__chart {
    margin: 72px 0;
  }
}

.test__title {
  margin-bottom: 40px;
}

.test__wrapper {
  outline: 1px solid var(--color-grey);
  border-radius: var(--block-border-radius);
  background: linear-gradient(0deg, var(--color-white) 80%, var(--color-grey) 100%);
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  height: auto;
  margin: 0 auto;
  display: flex;
  position: relative;
  overflow: visible;
}

.test__wrapper_closed {
  background: linear-gradient(0deg, var(--color-white) 0%, var(--color-grey) 100%);
  height: 160px;
  overflow: hidden;
}

.test__subtitle {
  font-size: var(--base-font-size);
  text-align: center;
  margin-bottom: 100px;
  padding-top: 32px;
}

.test__paragraph {
  padding: 0 80px;
}

.test__paragraph:before {
  content: "";
  background: url("quiz.42613fda.svg");
  width: 40px;
  height: 40px;
  display: block;
  position: absolute;
  left: 20px;
}

.test__button {
  position: absolute;
  bottom: 16px;
}

.test__inner {
  outline: 1px solid var(--color-black);
  border-radius: var(--block-border-radius);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 35px;
  width: 100%;
  max-width: 614px;
  margin: 52px auto 80px;
  padding: 60px;
  display: flex;
}

.test__inner.test-finished {
  padding: 20px 60px;
}

.test__question {
  font-weight: 500;
}

.test__answers {
  gap: 10px;
  display: flex;
}

.test__inner.test-finished .test__answers {
  flex-direction: column;
}

.test__inner.test-finished .test__answers p:after {
  content: ";";
}

.test__inner.test-finished .test__answers p:last-child:after {
  content: ".";
}

.test__answer_hidden {
  visibility: hidden;
  display: none;
}

.test__inner.test-finished .test__progress:after {
  content: "";
  vertical-align: middle;
  background: url("done.f0844a54.svg") center / contain no-repeat;
  width: 25px;
  height: 25px;
  margin-left: 5px;
  display: inline-block;
}

@media (width <= 1024px) {
  .test__wrapper {
    padding: 0 16px;
  }

  .test__wrapper_closed {
    height: 170px;
  }

  .test__wrapper_closed .test__subtitle {
    margin-bottom: 120px;
  }

  .test__subtitle {
    margin-bottom: 80px;
  }

  .test__button {
    position: absolute;
    bottom: 26px;
  }

  .test__paragraph {
    padding: 0 0 0 50px;
  }

  .test__paragraph:before {
    left: 10px;
  }

  .test__question {
    text-align: center;
    height: 80px;
  }

  .test__answer {
    padding: 15px 30px;
  }

  .test__answers_test-finished p {
    text-align: left;
  }
}

footer {
  justify-content: center;
  padding-bottom: 20px;
  display: flex;
}

.footer-links {
  gap: 60px;
  padding: 0;
  display: flex;
}

.footer-links__link {
  align-items: center;
  gap: 15px;
  display: inline-flex;
}

.cookie-notification {
  background-color: var(--color-grey);
  border-radius: var(--block-border-radius);
  justify-content: center;
  align-items: center;
  gap: 50px;
  max-width: 70%;
  height: 60px;
  margin: 0 auto;
  display: flex;
  position: fixed;
  bottom: 10px;
  left: 0;
  right: 0;
}

.notification_visible {
  opacity: 1;
  visibility: visible;
  transition: opacity .1s linear, visibility .1s linear;
}

.notification_hidden {
  opacity: 0;
  visibility: hidden;
  transition: opacity .1s linear, visibility .1s linear;
}

.cookie-notification__content {
  font-size: calc(var(--base-font-size) * 1.1);
  margin: 0;
}

.cookie-notification__button {
  font-size: calc(var(--base-font-size) * .9);
}

@media (width <= 1024px) {
  .cookie-notification {
    flex-direction: column;
    gap: 15px;
    height: auto;
    padding: 20px;
  }

  .cookie-notification__content {
    text-align: center;
    font-size: 20px;
  }

  .cookie-notification__button {
    font-size: calc(var(--base-font-size) * .8);
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark;
    --color-dark-scheme-grey: #5c616a;
    --color-dark-scheme-light-grey: #6b707b;
    --color-light-black: #212121;
    --color-dark-canvas-bg: #3b3e441a;
  }

  a {
    color: var(--color-white);
  }

  svg {
    fill: var(--color-dark-scheme-grey);
  }

  .header__menu-button svg {
    stroke: var(--color-dark-scheme-grey);
  }

  canvas {
    background-color: var(--color-dark-canvas-bg);
    border-radius: var(--block-border-radius);
  }

  .header__menu, .intro__more-link {
    background-color: var(--color-dark-scheme-grey);
  }

  .test__wrapper {
    border-color: var(--color-black);
    background: var(--color-dark-scheme-grey);
  }

  .cookie-notification {
    box-shadow: 0 0 5px 0 var(--color-black);
    background-color: var(--color-dark-scheme-grey);
  }

  @media (width >= 640px) {
    svg:hover {
      fill: var(--color-dark-scheme-light-grey);
    }

    .button:hover {
      background-color: var(--color-light-black);
    }

    .header__menu-button svg:hover {
      stroke: var(--color-dark-scheme-light-grey);
    }

    .intro__more-link:hover {
      background-color: var(--color-dark-scheme-light-grey);
    }

    .footer-links__link:not(.select-none):hover:after {
      background-color: var(--color-white);
    }
  }
}
